import React from "react";
import s from '../Nav_Bar/Nav_Bar.module.css';
import {NavLink, Route} from "react-router-dom";
import ProfileContainer from "../Profile/ProfileContainer";
/*
import FriendList from "./FriendList/FriendList";
import {API} from "../API/Api";*/

const Nav_Bar = (props) => {

    return (
        <nav className={s.nav}>
            <div className={s.item}>
                <NavLink to={'/profile/' + `12574`} activeClassName={s.active}>
                    Profile
                </NavLink>
            </div>
            <div className={s.item}>
                <NavLink to ="/dialogs" activeClassName={s.active}>
                    Messages
                </NavLink>
            </div>
            <div className={s.item}>
                <NavLink to ="/news" activeClassName={s.active}>
                    News
                </NavLink>
            </div>
            <div className={s.item}>
                <NavLink to ="/music" activeClassName={s.active}>
                    Music
                </NavLink>
            </div>
            <div className={s.item1}>
                <NavLink to ="/users" activeClassName={s.active}>
                    Users
                </NavLink>
            </div>
            <div className={s.item1}>
                <NavLink to ="/settings" activeClassName={s.active}>
                    Settings
                </NavLink>
            </div>

            <div className={s.item2}>
           {/* <FriendList API={API} users={props.users} pageSize={props.pageSize}
                            totalUsersCount={props.totalUsersCount} isFollowed={props.isFollowed} setUsers={props.setUsers}/>*/}
            </div>
        </nav>
    );

}

export default Nav_Bar;
