import React from "react";
import s from './Post.module.css';

const Post = (props) => {

    return (
        <div key={props.postId} className={s.post}>
            <div className={s.post_picture}>
                <img
                    src="https://openclipart.org/image/800px/svg_to_png/27714/JazzyNico-Air-traffic-control-Navpoint-circle-4.png"
                    className={s.post_picture_size}/>
            </div>
            <div className={s.post1}>
                {props.message}
            </div>
            <span>
                <button type='button' onClick={() => props.remove(props.postId)}> удалить пост {props.id} </button>
            </span>
                <div className={s.likes}>

                   < div className={s.like_style}>
                             <img
                            src="https://static.vecteezy.com/system/resources/previews/000/572/583/original/heart-icon-vector-illustration.jpg"
                            className={s.like_png_width}/>
                   {props.value}</div>

                </div>
        </div>)
        ;


}

export default Post
