import React from "react";
import s from './Profile.module.css';
import ProfileInfo from "./ProfileInfo/ProfileInfo";
import MyPostsContainer from "./MyPosts/MyPostsContainer";


const Profile = (props) => {



    return (
        <div className={s.content}>
            <div className={s.uppic1}>
                <img
                    src='https://avatars.mds.yandex.net/get-zen_doc/100325/pub_5ae83b0c77d0e6504bce8117_5ae86dcac3321b1f23ef3b0c/scale_1200'
                    className={s.uppic}/>
            </div>
           <ProfileInfo userPage={props.userPage} status={props.status} setStatus={props.setStatus} />
            <MyPostsContainer  />
        </div>

    );
}



export default Profile;
