import React, {useEffect, useState} from "react";

const StatusWithHook = (props) => {

    let [editMode, setEditMode] = useState(false);
    let [status, setStatus] = useState(props.status);

    useEffect(() => {
            setStatus(props.status)}, [props.status]);

    const activateEdit = () => {
        setEditMode(true)
    }

    const deactivateEdit = () => {
        setEditMode(false)
        props.setStatus(status)
    }

    const onStatusChange = (e) => {
        setStatus(e.currentTarget.value)
    }


    return (<>
            {!editMode &&
            <div>
                <span onDoubleClick={activateEdit}>{props.status || 'У этого пользователя нет статуса :c'}</span>
            </div>
            }
            {editMode &&
            <div>
                <input onChange={onStatusChange} onBlur={deactivateEdit} type='text' autoFocus={true} value={status}
                />
            </div>
            }
        </>
    )
}


export default StatusWithHook;