import React from "react";
import Profile from "./Profile";
import {connect} from "react-redux";
import {getStatusThunk, profileThunk, setUserProfile, updateStatusThunk} from "../../redux/profileReducer";
import {withRouter} from "react-router-dom";
import {compose} from "redux";
import {withAuthRedirect} from "../HOC/withAuthRedirect";


class ProfileContainer extends React.Component {


    componentDidMount() {
        let {profileThunk,getStatusThunk} = this.props
        let userId = this.props.match.params.userId;
        if (!userId){
            userId = 12574;
            if (!userId){
                this.props.history.push('/login')
            }
        }
            profileThunk(userId);
            getStatusThunk(userId)
    }
    render() {
        return <>
            <Profile userPage={this.props.userPage} userId={this.userId} status={this.props.statusText} setStatus={this.props.updateStatusThunk}/>
        </>
    }

    componentDidUpdate(prevProps, prevState, snapshot) {
        let {profileThunk} = this.props
        if(this.props.match.params.userId != prevProps.match.params.userId) {
            let userId = 12574
            profileThunk(userId)}
    }}


let mapStateToProps = (state) => {
    return {
        userPage: state.profilePage.userPage,
        statusText: state.profilePage.statusText
    }
};


export default compose(withAuthRedirect,
    withRouter,
    connect(mapStateToProps,
        {setUserProfile, profileThunk, getStatusThunk,updateStatusThunk}))(ProfileContainer);
