import React from "react";
import s from './MyPosts.module.css'
import Post from "./Post/Post";
import {Field, Form, Formik} from "formik";


const MyPosts = React.memo(props => {


    let postsElements = props.posts.map(p => (<Post remove={props.deletePost} postId={p.postId} message={p.message} value={p.likesCount}/>));

    let addPost = (text) => {
        props.addPostActionCreator(text);
    }

    return (


        <div className={s.posts}>
            <div className={s.input_posts}>
                <div className={s.myposts}>
                    Posts:
                </div>
                <div className={s.input}>
                    <Formik
                        initialValues={{
                            postText: '',

                        }}
                        onSubmit={async (values, {resetForm}) => {
                            if (values.postText != '') {
                                addPost(values.postText)
                                resetForm({})
                            }
                        }}
                    >
                        {({values}) => (
                            <Form>
                                <div className={s.input}>
                                    <Field id={'postText'} className={s.input_text} name="postText"
                                           placeholder="Введите текст поста..." type="text"/>
                                </div>
                                <div className={s.input1}>
                                    <button className={s.input_button} type={"submit"}> отправить</button>
                                </div>
                            </Form>
                        )}
                    </Formik>
                </div>
            </div>

            <div className={s.post}>
                {postsElements}
            </div>
        </div>
    );

})

export default MyPosts;
