import React from "react";
import s from '../Header/Header.module.css'
import {NavLink} from "react-router-dom";



const Header = (props) => {
    let login  = <NavLink to={'/login'} className={s.login}>Login</NavLink>
    return (<>


        <header className={s.header}>
             <img src='https://smajlik.ru/wp-content/uploads/2017/12/2-1024x816.png'/>

            {props.isAuth ? <div className={s.login1}>{props.login} <button onClick={props.logoutThunk}> выйти </button></div> : <button onClick={login}> войти </button> }

        </header>
</>
    );

}
export default Header;
